
@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@300&family=Roboto&family=Ubuntu:ital,wght@0,400;1,300&display=swap');


*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "outfit" , sans-serif;
}
body{
  background-color: black;
}

h1 , h4 ,p,a{
  color: white;
  text-decoration: none;
}
ul{
  list-style-type: none;
}
.btn{
  padding: 12px 32px;
  font-size: 1rem;
  text-transform: uppercase;
  background-color: green;
  color: #222;
  border: 1px solid #fff;
  font-weight: 600;
  cursor: pointer;
}
.btn-light {
  /* background: rgba(255, 255, 255, 0.2); */
  background: transparent;
  color: #fff;
}

.btn:hover {
  background: rgba(255, 255, 255, 0.2);
  color: #fff;
  transition: 0.3s;
}