.pricing{
    width: 100%;
    padding: 6rem;
    background: #000;
}
.card-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3 , 1fr);
    grid-gap:40px
}
.card{
    border: 1px solid #eee;
    color: #eee;
    text-align: center;
    padding: 1rem;
}
.card:hover{
    background-color: #141414;
}
.card h4{
    font-size: 1.4rem;
    padding: 1rem;
}
.card p{
    padding: 1rem;
}
.bar{
    border-bottom: 1px solid #eee;
    width: 10%;
    margin: 1.5rem auto;
    display: block;

}

.btc{
    font-size:4rem;
    font-weight: 600;
    padding: 1rem 0;
}
.card .btn{
     display: block;
     width: 80%;
     margin: 2rem auto;
}


@media screen and (max-width:700px) {
    .card-container{
        max-width: 90%;
        margin: auto;
        display: block;
        grid-template-columns: repeat(1fr);
       
    }
    .card{
        margin-top: 4rem;
    }
}